@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
@import url(https://fonts.googleapis.com/css?family=Play:regular,700);
@charset "UTF-8";

* {
   padding: 0;
   margin: 0;
   border: 0;
}

*,
*:before,
*:after {
   box-sizing: border-box;
}

:focus,
:active {
   outline: none;
}

a:focus,
a:active {
   outline: none;
}

nav,
footer,
header,
aside {
   display: block;
}

html,
body {
   height: 100%;
   width: 100%;
   font-size: 100%;
   line-height: 1;
   font-size: 14px;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
   font-family: inherit;
}

input::-ms-clear {
   display: none;
}

button {
   cursor: pointer;
}

button::-moz-focus-inner {
   padding: 0;
   border: 0;
}

a,
a:visited {
   text-decoration: none;
}

a:hover {
   text-decoration: none;
}

ul li {
   list-style: none;
}

img {
   vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-size: inherit;
   font-weight: 400;
}

* {
   min-height: 0;
   min-width: 0;
}

body {
   color: #fff;
   font-size: 14px;
   font-family: 'Roboto';
   background-color: #18171d;
}

body._lock {
   overflow: hidden;
}

a {
   color: #fff;
}

.btn {
   display: inline-flex;
   align-items: center;
   justify-content: center;
}

button[disabled] {
   background-color: #252525;
}

.wrapper {
   width: 100%;
   min-height: 100%;
   overflow: hidden;
   display: flex;
   background-color: #18171d;
   flex-direction: column;
}

._container {
   max-width: 1440px;
   margin: 0 auto;
   padding: 0px 20px;
}

.rub:after {
   content: '₽';
}

.header {
   background-color: #222129;
}

.header__container {
   height: 50px;
   display: flex;
   align-items: center;
}

.header__logo {
   flex: 0 0 82px;
}

.header__logo img {
   max-width: 100%;
}

.header__search {
   flex: 1 1 auto;
   padding: 0px 0px 0px 170px;
}

.header__nav {
   flex: 1 1 auto;
}

.search-header__input {
   position: relative;
   width: 352px;
   height: 28px;
}

.search-header__input .input {
   width: 100%;
   height: 100%;
   border-radius: 10px;
   border: 1px solid #403d3d;
   background-color: #252525;
   color: #fff;
   padding: 0px 20px 0px 37px;
}

.search-header__icon {
   position: absolute;
   top: 50%;
   left: 10px;
   -webkit-transform: translate(0px, -50%);
   transform: translate(0px, -50%);
}

.nav-header__list {
   display: flex;
}

.nav-header__list li {
   margin: 0px 20px;
}

.nav-header__link {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   min-height: 37px;
   min-width: 37px;
   border-radius: 50%;
   border: 1px solid #403d3d;
   background-color: #252525;
}

.user-header__body {
   position: relative;
}

.user-header {
   margin: 0px 15px;
}

.user-header__ava {
   width: 51px;
   padding: 0px 15px 0px 0px;
   margin: 0px 0px 0px 15px;
}

.user-header__ava img {
   border: 1px solid #403d3d;
   border-radius: 50%;
   max-width: 100%;
}

.user-header__arrow {
   position: absolute;
   right: 0;
   top: 50%;
   margin: 0px 0px 0px 4px;
}

.page {
   background-color: #18171d;
   flex: 1 1 auto;
   padding: 20px 0px 0px 0px;
}

.page__side {
   flex: 0 0 260px;
   background-color: #1b1a1a;
   align-self: flex-start; /* Внести свойство в реальный проект */
}

.page__content {
   flex: 1 1 auto;
   padding: 0px 0px 0px 20px;
}

.info-profile {
   display: flex;
   margin: 0px 0px 30px 0px;
}

.info-profile__column:nth-child(1) {
   background-color: #1b1a1a;
   flex: 0 0 256px;
   padding: 15px;
   border: 1px solid #403d3d;
}

.info-profile__column:nth-child(2) {
   flex: 1 1 auto;
   padding: 0px 0px 0px 20px;
}

.info-profile__ava img {
   width: 100%;
   height: 100%;
}
.info-profile__name {
   font-size: 18px;
   font-weight: 700;
   letter-spacing: 0.01em;
   margin: 0px 0px 16px 0px;
}

.info-profile__status {
   display: flex;
   flex-direction: column;
   margin: 0px 0px 30px 0px;
}
.status-profile__text {
   line-height: 18px;
   cursor: pointer;
}
.status-profile__btn {
   display: flex;
   justify-content: center;
   align-items: center;
   color: #fff;
   padding: 0px 20px;
   min-height: 30px;
   border: 1px solid #403d3d;
   background-color: #252525;
}

.status-profile__input .input {
   background-color: #252525;
   padding: 7px;
}
.status-profile__input {
   margin: 0px 0px 10px 0px;
}
.status-profile {
   position: relative;
}
.status-profile__body {
   position: absolute;
   top: -10px;
   left: 0;
   min-width: 400px;
   min-height: auto;
   border: 1px solid #403d3d;
   background-color: #1b1a1a;
   padding: 10px;
}
.new-post {
   margin: 0px 0px 65px 0px;
}

.new-post__body {
   position: relative;
   width: 400px;
   height: 80px;
}

.new-post__message {
   padding: 5px 5px 30px 5px;
   color: #fff;
   background-color: #1b1a1a;
   border: 1px solid #403d3d;
   width: 100%;
   height: 100%;
}

.new-post__actions {
   position: absolute;
   bottom: 5px;
   right: 5px;
   display: flex;
}

.new-post__action {
   margin: 0px 10px;
}

@media (min-width: 991.98px) {
   .btn:hover {
      background-color: #bd6d3c;
   }
}

@media (max-width: 640px) {
   ._container {
      padding: 0px 10px;
   }
}

