.menu-page {
  padding: 0px 0px 50px 0px;
}

.menu-page__list li {
  display: block;
  margin: 0px 0px 0px 0px;
}

.menu-page__link {
  position: relative;
  padding: 13px 25px;
  display: flex;
  align-items: center;
  font-size: 16px;
  letter-spacing: 0.5px;
}

.menu-page__link span {
  transition: all 0.15s ease 0s;
  flex: 1 1 auto;
  padding: 0px 0px 0px 13px;
}

.menu-page__link:before {
  transition: all 0.15s ease 0s;
  content: "";
  height: 80%;
  width: 5px;
  background-color: transparent;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.menu-page__link:hover .menu-page__icon,
.menu-page__link._active .menu-page__icon {
  fill: #2ab95e;
}

.menu-page__link:hover span,
.menu-page__link._active span {
  color: #fff;
}

.menu-page__link._active:before {
  background-color: #2ab95e;
}

.menu-page__link:hover:not(.menu-page__link._active) {
  background-color: rgba(102, 101, 107, 0.1);
}

.menu-page__icon {
  width: 18px;
  height: 18px;
  fill: #66656b;
}

.page__menu {
  flex: 0 0 250px;
  background-color: #222129;
}

.page__content {
  flex: 1 1 auto;
  padding: 0px 0px 0px 20px;
}
